var timer = null
import {
	CARDPACK
} from '../../../utils/api.js'
import uniLoadMore from '../../../components/uni-load-more/uni-load-more.vue'
export default {
	components: {
		uniLoadMore
	},
	filters: {
		formateDate: function(value) {
			var first = value.split(" ")[0]
			if (first !== undefined) {
				return first
			}
		},
		formateType(value) {
			var result
			if (value == 30) {
				result = '月卡'
			} else if (value == 90) {
				result = '季卡'
			} else if (value == 360) {
				result = '年卡'
			}
			return result
		}
	},
	onPullDownRefresh() {
		this.page = 1
		this.getCardList()
	},
	data() {
		return {
			available: true,
			cardList: [],
			page: 1,
			now: '',
			load: 'more'
		}
	},
	onReachBottom: function() {
		var _this = this
		if (timer != null) {
			clearTimeout(timer);
		}
		timer = setTimeout(function() {
			_this.getmore();
		}, 1000);
	},
	onLoad() {
		uni.showToast({
			title: '加载中...',
			icon: 'none',
			mask: true
		})
		this.getCardList()
		this.getTime()
	},
	methods: {
		// getCardList
		getCardList() {
			const params = {
				page: this.page
			}
			this.$request.url_request(CARDPACK, params, 'GET', res => {
				this.cardList = JSON.parse(res.data).data.records
				uni.stopPullDownRefresh()
				uni.hideToast()
			}, err => {})
		},
		// 加载更多
		getmore() {
			if (this.load == 'more') {
				this.todayText = 'loading'
				this.$forceUpdate()
				this.page++
				const params = {
					page: this.page
				}
				this.$request.url_request(CARDPACK, params, 'GET', res => {
					if (JSON.parse(res.data).data.records.length <= 0) {
						this.load = 'noMore'
						this.$forceUpdate()
					} else {
						this.cardList = this.cardList.concat(JSON.parse(res.data).data.records)
						this.load = 'more'
						this.$forceUpdate()
					}
				}, err => {})
			}


		},
		// getTime
		getTime() {
			var myDate = new Date();
			var year = myDate.getFullYear();
			var month = myDate.getMonth() + 1;
			var date = myDate.getDate();
			var h = myDate.getHours();
			var m = myDate.getMinutes();
			var s = myDate.getSeconds();
			this.now = year + '-' + this.conver(month) + "-" + this.conver(date) + " " + this.conver(h) + ':' + this.conver(m) +
				":" + this.conver(s);
			console.log(this.now)
		},
		conver(s) {
			return s < 10 ? '0' + s : s;
		},
		// 卡密详情
		camiDetail(id) {
			uni.navigateTo({
				url: '../camiDetail/camiDetail?id=' + id
			})
		}
	}
}
